//* - [Variables] -
$maxWidthContainer: 1920px;
//* - [Font Variables] -
$font-path: '../fonts';
$font-defoult: 'averta-regular', sans-serif;
$font-size: 16px;
$font-family-third: 'Inter', sans-serif;
$font-weight-defoult: 400;
//* - [Font color] -
$font-color-defoult: #000;
$font-color-secondary: #fff;
//* - [Color] -
$color: #2b3769;
$bg-color: #fff;
$second-color: #171b1c;
//* - [Other colors] -
$text-selection: #0054633f;
$text-color-selection: #9b9999;
//* -
:root {
  --clip: left center;
  --size: 0;

  //* - [Scrollbar] -

  --background-thumb: #6980dd;
  --scrollbar-thumb-bgcolor: #aeb9e791;
  --border-color: $color;
  --themes-color: #2b3769;
  // ---------------------------------------------------------------------------
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;

  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
  --background-image: background: url('../img/portfolio/project-smt/fon.png')
    center / cover no-repeat;
  --radial-gradient:
    radial-gradient(
      ellipse at 17% 15%,
      #171b1c 25%,
      #171b1c67 60%,
      transparent 100%
    ),
    radial-gradient(circle at 40% 140%, #eeeeee96 0%, #2b3769 30%, transparent),
    radial-gradient(
      circle at 100% 100%,
      #eeeeee4d 0%,
      #2b3769 20%,
      transparent
    ),
    radial-gradient(
      ellipse at 30% 2%,
      #171b1c 70%,
      rgb(43, 55, 105) 100%,
      transparent
    ),
    radial-gradient(
      circle at center left,
      rgba(238, 238, 238, 0) 10%,
      rgb(4, 5, 10) 50%,
      transparent
    );
}

$snap: cubic-bezier(1, 0.005, 0.24, 1);
$bounce: cubic-bezier(0.175, 0.885, 0.32, 1.275);
.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}
.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}
