@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #6980dd;
  --scrollbar-thumb-bgcolor: #aeb9e791;
  --border-color: $color;
  --themes-color: #2b3769;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
  --background-image: background: url("../img/portfolio/project-smt/fon.png")
    center / cover no-repeat;
  --radial-gradient:
    radial-gradient(
      ellipse at 17% 15%,
      #171b1c 25%,
      #171b1c67 60%,
      transparent 100%
    ),
    radial-gradient(circle at 40% 140%, #eeeeee96 0%, #2b3769 30%, transparent),
    radial-gradient(
      circle at 100% 100%,
      #eeeeee4d 0%,
      #2b3769 20%,
      transparent
    ),
    radial-gradient(
      ellipse at 30% 2%,
      #171b1c 70%,
      rgb(43, 55, 105) 100%,
      transparent
    ),
    radial-gradient(
      circle at center left,
      rgba(238, 238, 238, 0) 10%,
      rgb(4, 5, 10) 50%,
      transparent
    );
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

